
 @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
 @import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');
 * {
	 box-sizing: border-box;
	 margin: 0;
	 padding: 0;
}
 @keyframes myAnim {
	 0%, 50%, 100% {
		 opacity: 1;
	}
	 25%, 75% {
		 opacity: 0;
	}
}
 body {
	 font-family: 'Inconsolata', monospace;
}
 .container {
	 max-width: 500px;
	 margin: 30px auto;
	 overflow: auto;
	 min-height: 300px;
	 border: 1px solid steelblue;
	 padding: 30px;
	 border-radius: 5px;
	 background: #38598b;
	 color: #fff;
	 border: 10px solid #a2a8d3;
	 border-radius: 20px;
	 box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
}
 .header {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 margin-bottom: 20px;
}
 .btn {
	 display: inline-block;
	 background: #000;
	 color: #fff;
	 border: none;
	 padding: 10px 20px;
	 margin: 10px 5px;
	 border-radius: 5px;
	 cursor: pointer;
	 text-decoration: none;
	 font-size: 15px;
	 font-family: 'Inconsolata', monospace;
	 width: 100%;
	 box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
}
 .btn {
	 font-family: 'Inconsolata', monospace;
}
 .btn:focus {
	 outline: none;
}
 .btn:active {
	 transform: scale(0.98);
}
 .btn:hover {
	 filter: brightness(1.2);
}
 .btn-block {
	 display: block;
	 width: 100%;
}
 .task {
	 background: #f4f4f4;
	 width: 100%;
	 margin: 5px;
	 padding: 10px 20px;
	 cursor: pointer;
	 color: #000;
	 box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
	 transition: background var(2s);
}
 .task:hover {
	 background-color: #ffcab0;
	 transition: 200ms;
}
 .task.reminder {
	 border-left: 10px solid green;
}
 .task h3 {
	 display: flex;
	 align-items: center;
	 justify-content: space-between;
}
 .add-form {
	 margin-bottom: 40px;
	 -webkit-animation: fadein 2s;
	/* Safari, Chrome and Opera > 12.1 */
	 -moz-animation: fadein 2s;
	/* Firefox < 16 */
	 -ms-animation: fadein 2s;
	/* Internet Explorer */
	 -o-animation: fadein 2s;
	/* Opera < 12.1 */
	 animation: fadein 500ms;
	 position: relative;
	 transition: all 0.3s ease-in-out;
}
 @keyframes fadein {
	 from {
		 opacity: 0;
	}
	 to {
		 opacity: 1;
	}
}
/* Firefox < 16 */
 @-moz-keyframes fadein {
	 from {
		 opacity: 0;
	}
	 to {
		 opacity: 1;
	}
}
/* Safari, Chrome and Opera > 12.1 */
 @-webkit-keyframes fadein {
	 from {
		 opacity: 0;
	}
	 to {
		 opacity: 1;
	}
}
/* Internet Explorer */
 @-ms-keyframes fadein {
	 from {
		 opacity: 0;
	}
	 to {
		 opacity: 1;
	}
}
/* Opera < 12.1 */
 @-o-keyframes fadein {
	 from {
		 opacity: 0;
	}
	 to {
		 opacity: 1;
	}
}
 .form-control {
	 margin: 20px 0;
}
 .form-control label {
	 display: block;
}
 .form-control input {
	 width: 100%;
	 height: 40px;
	 margin: 5px;
	 padding: 3px 7px;
	 font-size: 17px;
	 display: flex;
}
 input::placeholder {
	 font-family: 'Inconsolata', monospace;
}
 .form-control-check {
	 display: flex;
	 align-items: flex-end;
	 justify-content: space-between;
}
 .form-control-check label {
	 flex: 1;
}
 .form-control-check input {
	 flex: 2;
	 height: 20px;
}
 footer {
	 margin-top: 30px;
	 text-align: center;
}